@import url(https://fonts.googleapis.com/css?family=Gudea&display=swap);
.navbar {
  display: grid;
  padding: 0 50px 0 0;
  margin: 0;
  text-align: center;
  list-style-type: none;
  grid-template-columns: 1fr 100px 100px 100px;
  grid-template-rows: 25px;
  grid-template-areas: ". about projects food-travel";
}

.nav-link {
  grid-row-start: 1;
}

.about {
  grid-area: about;
}

.projects {
  grid-area: projects;
}

.food-travel {
  grid-area: food-travel;
}
li {
  list-style: none;
}
body {
  margin: 0;
  font-family: 'Gudea', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  margin: 0;
}

a {
  text-decoration: none;
  color: unset;
}

a:hover {
  color: #ff00006e;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  border-bottom: 1px solid black;
}

.header .logo {
  height: 50px;
  width: 50px;
  display: block;
  font-size: 40px;
  text-align: center;
  line-height: 40px;
}

.app-body {
  width: 90%;
  margin: 3vw auto 0;
}
